body {
  width:533px;
  height: 720px;
  background-color: white;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
}

.loader {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 300;
}

.title {
  margin: 0;
  background: #22b8cf;
  color: white;
  height: 4rem;
  font-size: 1.5rem;
  font-weight: unset;
  display: flex;
  align-items: center;
  justify-content: center;
}

.search_div {
  display: flex;
}

.input_search {
  background: #495057;
  outline: none;
  border: none;
  padding: 0.5rem;
  font-size: 1.125rem;
  line-height: 1.5;
  color: white;
  flex: 1;
}

.input_search::placeholder {
  color: #dee2e6
}

.addButton {
  background: none;
  outline: none;
  border: none;
  background: #868e96;
  color: white;
  padding-left: 1rem;
  padding-right: 1rem;
  font-size: 1.5rem;
  display: flex;
  align-items: center;
  cursor: pointer;
  transition: 0.1s background ease-in;
}

.addButton:hover {
  background: #adb5bd;
}
.movie_container{
  width:512px;
  height: 720px;
  background-color: white;
  margin-top: 20px;
  margin-left: auto;
  margin-right: auto;
}
.movies_div {
  width: 120px;
  height: 160px;
}
.movies_div > div {
  width: 120px;
  height: 160px;
}
.movies_div > div > img {
  width: 102.3px;
}

.inp_button{
  border-top: 100px;
  justify-content: center;
  width: 35px;
  margin: 0 9px;
  align-items: center;
  margin-top: 5;
  background-color: #45B4CC;
  border-radius: 5px;
  color: whitesmoke;
}

