.movie {
  background-color: white;
  height: 550px;
  width: 100%;
}

.info_background{
  background-color: rgb(20, 20, 20);
  padding: 0 0 10px 20px;
  color: white;
}

.movie_data {
  background-image: url("");
  display: table;
  float: left;
  position: relative;
  width: 533px;
}

.movie_image{
  margin-top: 20px;
  background-size: cover;
  background-position: center;
}

.movie_info{
  margin: 0;
  font-size: 18px;
  font-weight: bolder;
  padding-top: 30px;
}

.movie_sub_info{
  color: white;
  margin: 0;
  display:flex;
}

.movie_rating{
  margin: 16px 145px 0 155px;
}

.movie_summary{
  font-size: 18px;
  font-weight: bolder;
  color: white;
}

.movie_sub_summary{
  padding-right: 15px;
}

.movie_title{
  color: white;
  padding: 300px 0 0px 0px;
}

.movie_genres{
  margin: 0;
  padding: 0 0 10px 20px;
}

.movie_name{
  margin: 0;
  padding: 10px 0 10px 20px;
  font-size: 23px;
  font-weight:bolder;
}

